
/*  SECTION EXPLAIN */
.SectionExplain {
    position: relative;
    display: flex;
    min-height: 505px;
    padding: 0 0 50px 0;
    /* width: 100vw; */
    margin: 50px auto;
    background-color: #fff;
}
.InExplain {
    position: relative;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    flex-direction: row;
    text-align: center;
    /* width: 1200px; */
}
.ButtonDiv {
    position: relative;
    width: 100%;
}
.DB {
    position: relative;
    background-color: #009fd9;
    font-size: 20px;
    color: #fff;
    border-radius: 10px;
    width: 100%; 
    /*DELETE*/
}
.RowImage {
    max-width: 400px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.InExplain img {
    position: relative;
    /* width: 400px;
    height: 350px; */
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.ColBack {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    padding: 0px 20px;
    justify-content: center;
}
.InBack {
    position: relative;
}
.InBack h1 {
    font-size: 40px;
    font-weight: 700;
}
.InBack p {
    font-size: 20px;
    font-weight: 500;
    padding-bottom: 30px;
}
.PicButton, .PicButtonTwo {
    position: relative;
    width: 60%;
    align-self: center;
    text-align: center;
    color: white;
    text-decoration: none;
    background-color: #009fd9;
    padding: 15px 60px;
    font-size: 1;
    border-radius: 5px;
    transition-duration: 0.2s;
}
.PicButtonTwo {
    background-color: #fff;
    color: #009fd9;
    border: 3px solid #009fd9;
    transition-duration: 0.2s;
    margin-top: 10px;
    padding: 10px;
}
.PicButtonTwo:hover {
    border-color: #0289b9;
    color: #0289b9;
}
.PicButton:hover {
    background-color: #0289b9;
}
/* MEDIA */
@media (max-width: 800px) {
    .ColBack {
        margin-bottom: 50px;
        max-width: 100%;
    }
    .PicButton, .PicButtonTwo {
        width: 100%;
        font-size: 1rem;
    }
    .InExplain {
        width: 100%;
        flex-direction: column;
        align-items: center;
        padding-bottom: 50px;
    }
 
}
@media (max-width: 600px) {
   /*section explain*/
    .SectionExplain {
        padding: 0;
        margin: 0;
        min-height: 55vh;
    }
    .InExpRow {
        padding: 0 70px;
    }
    .RowImage {
        max-width: 100%;
        padding: 15px;
    }
}