::-webkit-scrollbar {
    width: 4px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
    background: #d9f5ff; /* change color*/
}
.Desktop {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    
}
.List {
    position: relative;
    max-height: 100%;
    /* min-width: 220px!important;
    width: 220!important; */
    width: 100%;
    overflow: hidden;
    border-right: 1px solid #e2e2e2;
    display: flex;
    flex-direction: column;
}
.Messenger {
    display: flex;
    overflow: hidden;
    overflow-y: scroll;
    flex-direction: column;
    /* flex: 0.8; */
    max-height: 90%;
    height: 90%;
}
.Messages {
    position: relative;
    display: flex;
    flex-direction: column;
}
.EachTopRight {
    position: relative;
    width: 60%;
    padding: 10px;
    background: #009fd9;
    color: #fff;
    border-radius: 10px 10px 0 10px;
    right: 0;
    margin-right: 10px;
    margin-bottom: 10px;
    display: flex;
    align-self: flex-end;
    flex-direction: column;
}
.EachTopMiddle {
    position: relative;
    width: 100%;
    padding: 10px;
    background: #b4d6e2;
    color: #fff;
    border-radius: 5px;
    margin: 0 0 10px 0;
    display: flex;
    flex-direction: column;
}
.EachTopLeft {
    position: relative;
    width: 60%;
    padding: 10px;
    background: #e2e2e2;
    border-radius: 0 10px 10px 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    display: flex;
    align-self: flex-start;
    flex-direction: column;
    color: #000;
}
.EachMessage {
    position: relative;
    width: 100%;
    padding: 0 10px;
    margin-bottom: 10px;
    display: flex;
    align-self: flex-start;
    flex-direction: column;
    /* border-bottom: 1px solid #e2e2e2; */
    color: #000;
    /* cursor: pointer; 
    padding: 0 10px;*/
}
/* .EachMessage:hover {
    color: #009fd9;
    background-color: #e2e2e2;
} */
.EachTop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.List h5 {
    /* color: #606060; 
    color: #fff;*/
    margin: 5px 0;
}
.Messenger p {
    margin: 0 ;
}
.EachTop p, .EachTop h4 {
    margin: 0;
}
.DateLine {
    width: 100%; 
    text-align: center; 
    border-bottom: 1px solid #009fd9;
    color: #009fd9;
    line-height: 0.1em;
    margin-top: 15px!important;
    margin-bottom: 10px!important;
}
.DateLine span {
    background:#fff; 
    padding:0 10px; 
}
/* INPUT */
.BotMessage {
    width: 100%;
    /* bottom: 25px;
    left: 0;
    right: 0;
    position: absolute;
    height: 20%*/ 
    display: flex;
    flex-direction: row;
    border-top: 1px solid #e2e2e2;
    padding: 10px;
}
.Input {
    width: 100%;
    border: 0;
    height: 35px;
    font-size: 17px;
    padding: 10px;
}
input:focus {
    outline-width: 0;
}
.BotMessage img {
    height: 30px;
    width: 30px;
    cursor: pointer;
    margin-right: 20px;
}