.Desktop {
    position: relative;
    display: flex;
    /* width: 100%; 
    height: 100%;*/
    flex-direction: column;
    margin: 10px;
    
    /* box-shadow: 0 0 2rem rgba(0,0,0,.1); */
}
.MainTitle {
    color: #fff;
    background-color: #009fd9;
    padding: 10px;
    border-radius: 5px 5px 0 0;
}
.MainTitle p {
    margin: 0;
}
.List {
    position: relative;
    width: 100%;
    /* height: 100%; */
    border-radius: 0 0 5px 5px;
    background-color: #fff;
    display: flex;
}
.Recepti {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    /* flex-direction: column; */
}
.EachRecept {
    position: relative;
    border: 1px solid #e2e2e2;
    color: #606060;
    cursor: pointer;
    padding: 5px 10px;
    margin: 5px;
    /* box-shadow: 0px 0px 5px 0px rgba(148,148,148,1); */
}
.EachRecept:hover {
    color: #009fd9;
    background-color: #e2e2e2;
}
.EachTop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.List h4 {
    color: #606060;
    margin: 5px 0;
}
.List p {
    padding-left: 25px;
    color: #606060;
    margin: 0px 0 ;
}

@media (max-width: 600px) {
    .EachTop {
        flex-direction: column-reverse;
        align-items: flex-start;
    }
    .List p {
        padding-left: 0;
    }
    .EachTop p {
        padding-left: 0;
    }

  }