.Main {
    position: relative;
    display: flex;
    margin: 10px;
    flex-direction: column;
}
.MainTitle {
    color: #fff;
    background-color: #009fd9;
    padding: 10px;
    border-radius: 5px 5px 0 0;
}
.MainTitle p {
    margin: 0;
}
.MainInfo {
    position: relative;
    display: flex;
    background-color: #fff; 
    flex-wrap: wrap;
    border-radius: 0 0 5px 5px;
    padding: 10px;
}
.Info {
    border: 1px solid #e2e2e2;
    padding: 5px;
    margin: 5px;
    display: flex;
    align-items: center;
}
.Arrowimg {
    height: 17px;
    margin-left: 6px;
}
.Info p {
    margin: 0;
    color: #606060;
}
.Info span {
    color: #000;
    font-weight: 600;
}
.BmispanNotNormal {
    color: red!important;
    font-weight: 600;
}
.BmispanNormal {
    color: green!important;
    font-weight: 600;
}