.Desktop {
    position: relative;
    display: flex;
    width: 320px;
    height: 100%;
    flex-direction: column;
    margin: 10px;
    /* padding: 10px;
    border-radius: 5px;
    background-color: #fff;
     box-shadow: 0 0 2rem rgba(0,0,0,.1); */
}
.MainTitle {
    color: #fff;
    background-color: #009fd9;
    padding: 10px;
    border-radius: 5px 5px 0 0;
}
.MainTitle p {
    margin: 0;
}
.MainInfo {
    position: relative;
    display: flex;
    background-color: #fff; 
    flex-wrap: wrap;
    border-radius: 0 0 5px 5px;
    padding: 10px;
    justify-content: center;
}
.BeforeChart {
    position: relative;
    width: 100%;
    /* height: 200px; */
    height: auto;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
}
.InChart {
    display: flex;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
}
.InChartButton {
    padding: 5px;
    /* border: 1px solid #fff; */
    border-radius: 5px;
    color: #fff;
    background-color: #009fd9;
    margin: 0 15px 0 15px;
    box-shadow: 1px 3px 10px -5px #000;
    cursor: pointer;
}