.rbc-timeslot-group{
    min-height: 80px!important;
}
.Main {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.Popup {
    z-index: 5;
    position: absolute;
    height: 100%;
    width: 100%;
    /* --ion-background-color: 'rgb(226, 102, 108, 0.3)'; */
    background-color: rgb(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}
.InPopup {
    background-color: #fff;
    margin: auto;
    border-radius: 5px;
    height: 150px;
    width: 210px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    box-shadow: 0 4px 9px -4px black;
}
.InPopup p {
    margin: 0px;
}
.InPopup input {
    height: 30px;
    border: 0;
    border-bottom: 1px solid #009fd9;
}
.Buttons {
    display: flex;
    justify-content: flex-end;
}
.Button {
    background-color: #009fd9;
    margin-right: 10px;
    padding: 8px;
    border-radius: 10px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}
.Button:hover {
    background-color: #0289b9;
}