.Main {
    position: relative;
    display: flex;
    min-height: 100vh;
    width: 100%;
    background-color: #f0f0f0;
}
.Graphs {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* justify-content: center; */
}

/* ------------ SPINNER */
.WholeBox {
  position: relative;
  display: flex;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
}
.SpinnerBox {
    position: relative;
    height: 200px;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 2rem rgba(0,0,0,.1);
    flex-direction: column;
    color: #fff;
    font-weight: 500;
    background-color: #fff;
}
#Loading {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 3px solid rgba(255,255,255,.8);
    border-radius: 50%;
    border-top-color: #000;
    animation: spin 1s ease-in-out infinite;
    -webkit-animation: spin 1s ease-in-out infinite;
  }
  
  @keyframes spin {
    to { -webkit-transform: rotate(360deg); }
  }
  @-webkit-keyframes spin {
    to { -webkit-transform: rotate(360deg); }
  }
/*  ------------------------ */

@media (max-width: 600px) {
  .Main {
    height: 100%;
  }
}